import { Picker, PickerProps } from '@antmjs/vantui'
import { useState } from 'react'

const columns = [
  {
    text: '',
    id: 1,
    disabled: true,
  },
  {
    text: '',
    id: 2,
  },
  {
    text: '',
    id: 3,
  },
]
export default function Demo() {
  const [value, setValue] = useState<PickerProps['value']>(3)

  return (
    <Picker
      idKey="id"
      mode="content"
      showToolbar
      title=""
      value={value}
      columns={columns}
      onInput={(e) => {
        setValue(e.detail)
      }}
    />
  )
}
