import { View } from '@tarojs/components'
import { useRef, useState } from 'react'
import { Button, IPickerInstance, Picker, PickerProps } from '@antmjs/vantui'

const columns = ['', '', '', '', '']
export default function Demo() {
  const [value, setValue] = useState<PickerProps['value']>('')
  const pickerRef = useRef<IPickerInstance>(null)
  return (
    <View style="display: flex;">
      <Picker
        ref={pickerRef}
        value={value}
        onInput={(e) => {
          setValue(e.detail)
        }}
        mode="content"
        showToolbar
        title={
          <Button
            type="primary"
            hairline
            plain
            onClick={() => pickerRef?.current?.close()}
          >
            
          </Button>
        }
        columns={columns}
      />
      <Button type="primary" onClick={() => pickerRef?.current?.open()}>
        
      </Button>
    </View>
  )
}
