import { Picker, PickerProps } from '@antmjs/vantui'
import { useState } from 'react'

const columns = ['', '', '', '', '']
export default function Demo() {
  const [value, setValue] = useState<PickerProps['value']>('')
  return (
    <Picker
      value={value}
      onInput={(e) => {
        setValue(e.detail)
      }}
      mode="content"
      columns={columns}
    />
  )
}
